<template>
	<div :class="myimg">
		<div class="inner-img">
				<top-image :myimg="myimg"></top-image>
		</div>
		<div class="charper1">
			<large-search class="search-box"></large-search>
			<span class="main-text">{{mainText}}</span>
		</div>
		<router-view></router-view>
	</div>
</template>

<script type="text/ecmascript-6">
	import TopImage from 'components/top-image/top-image'
	import largeSearch from 'common/large-search/large-search'

	export default {
		data() {
			return {
				myimg: 'poruduct-center',
				mainText:"上海壮景化工有限公司（Shanghai Brightfield Chemical Co., Ltd.) ，是一家专注于塑料工业发展，以技术为导向的助剂，解决方案供应商。依托多年和国外助剂供应商的默契合作，依托多年和客户的贸易经验，壮景化工成为了专业服务于热塑性和热固性行业的可靠的特种化学品供应商，服务和成就众多国内外客户。"
			}
		},
		components:{
			TopImage,
			largeSearch
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
	@import "~common/stylus/variable"
	.charper1
		position: relative
		display: flex
		margin-bottom: 100px
		@media screen and (max-width:550px)
			margin-bottom: 50px
		.search-box
			width: 28%
			flex: 0 0 28%
			margin: -30px 0 0 100px
			@media screen and (max-width:550px)
				display: none
		.main-text
			padding: 0 100px
			margin-top: 30px
			font-size: $font-web-small
			line-height: $font-web-medium
			@media screen and (max-width:550px)
				font-size: $font-web-small
			@media screen and (max-width:750px)
				padding: 0 50px
				font-size: $font-web-small
</style>



