<template>
	<div class="select-1">
		<span class="cntitle">{{mainWord.chTitle}}</span>
		<span class="circle">.</span>
		<span class="entitle">{{mainWord.enTitle}}</span>
		<span class="mainword">{{mainWord.main}}</span>
		<div class="introduct-img">
			<div class="imgwrapper" v-for="item in mainImg">
				<img :src="'static/image/serive-img/' + item + '.jpg'"/>
			</div>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
export default {
	data() {
		return {
			mainImg: [1,2,3],
			mainWord: {
				chTitle:"实验室简介",
				enTitle:"Lab Intro",
				main: "实验室最初建立于2015年，坐落在张江高科技园区内， 拥有从事多年聚氨酯及塑料加工领域的经验，拥有转矩流变仪，高速搅拌机，匀质机及合成高压釜等应用实验设备。与高校及国内外知名化工企业客户都有良好的合作关系。产品涉及生物质材料，高性能膜材料，光电子产品材料的研发，并提供相关技术转让，技术咨询和技术服务，目前已在热固性塑料加工领域有若干专利，专利申请公布号：CN 104045836 A / CN 104974351 A 等等。"
			}
		}
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
	@import "~common/stylus/variable"
	
	.cntitle
		font-size: $font-web-medium
		text-align: center
		@media screen and (max-width:550px)
			font-size: $font-web-small-l
	.entitle
		font-size: $font-web-small
	.circle
		font-size: $font-web-large
	.mainword
		display: block
		line-height: $font-web-medium
		padding-top: 40px
		border-top: 1px solid $color-theme
		@media screen and (max-width:550px)
			font-size: $font-web-small-s
			padding-top: 20px
	.introduct-img
		display: flex
		justify-content: space-between
		margin-right: -20px
		@media screen and (max-width:550px)
			display: none
		.imgwrapper
			flex: 1
			img
				width: 100%
				margin: 80px auto 0
				padding-right: 20px
</style>



