<template>
	<div class="modified-plastics">
		<title-top></title-top>
		<div class="mainword">
			<div class="inculdtitle">
				<span class="titleword0">{{$t("chaper1[0]")}}</span>
				<div @click="toggleshow" class="svg0">
					<open-book></open-book>
				</div>
			</div>
			<div class="inculdtitle">
				<span class="titleword1">{{$t("chaper1[1]")}}</span>
			</div>
			<transition-group name="fade">
				<div :key="'mykey' + index" v-show="isshow" class="inner" v-for="(item, index) in stepLists">
					<span class="inner-text" v-for="x in item">{{x}}<i class=""></i></span>
				</div>
			</transition-group>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
	import titleTop from 'common/title-top/title-top'
	import openBook from 'common/only-svg/open-book'

	export default {
		data() {
			return {
				z: this.$t("chaper2"),
				mainWord: {},
				isshow: false
			}
		},
		components: {
			titleTop,
			openBook
		},
		created() {
	},
	methods: {
			toggleshow() {
				this.isshow = !this.isshow
			}
		},
		computed: {
			stepLists() {
				return this.$t("chaper2")
			}
		}
	}
</script>

<style lang="stylus" rel="stylesheet/stylus">
	@import "~common/stylus/variable"

	.mainword
		width: 80%
		margin: 0 auto
		span
			display: block
		.inculdtitle
			display: flex
			justify-content: space-between
			.titleword0
				color: $color-theme
				font-size: $font-web-medium-s
				margin-bottom: 30px 
			.svg0
				cursor: pointer
			.titleword1
				width: 90%
				margin: 0 auto 80px 
			.svg1
				display: none
		.fade-enter, .fade-leave-to
			transform: translate(1000px)
		.fade-enter-active
			transition: all 1s ease
		.fade-leave-active
			transition: 0.5s ease
		.inner
			margin-bottom: 20px
			padding: 5px 0
			span
				padding: 5px 0
				width: 90%
				margin: 0 auto
				border-bottom: 1px dashed $color-text-m
				font-size: $font-web-small-s
				&:nth-child(1)
					border-bottom: 1px solid $color-theme
					width: 100%
					font-size: $font-web-small
</style>



